import { Customizable, AccountInformation } from '@backbase/contact-common-ang';
import { ContactPutRequestBody as Contacts } from '@backbase/data-ang/contact-manager';

export interface ContactsFormItem extends Customizable {
  /**
   * contact ID
   */
  id: string;
  /**
   * Contact's first name
   */
  name: string;
  /**
   * Contacts lastname
   */
  surname?: string;
  /**
   * IBAN
   */
  IBAN?: string;
  /**
   * Contact's account Number.
   */
  accountNumber?: string;
  /**
   * contacts initials
   */
  initials?: string;
  /**
   * account Details.
   */
  accounts?: AccountInformation[];
  /**
   * contacts PhoneNumber.
   */
  phoneNumber?: string;
  /**
   * Contacts email ID.
   */
  email?: string;
}

export interface SearchBoxSelectedEvent {
  item: Contacts;
  preventDefault: any;
}

export interface SelectContactEvent {
  /**
   * Contacts ID
   */
  id: string;
  /**
   * Contacts type.
   */
  type: string;
}

export enum AccountCreateType {
  IBAN = 'IBAN',
  EMAIL = 'email',
  PHONE = 'phoneNumber',
  ACCOUNT = 'accountNumber',
}

export enum AccountTypeModelKey {
  IBAN = 'IBAN',
  BBAN = 'accountNumber',
}

export interface CreateListType {
  /**
   * List of type of Contacts to create from
   */
  listItem: AccountCreateType;
  /**
   * To check if its enabled.
   */
  enableItem: boolean | undefined;
}
